export default {
	data(){
		return{
			// 默认不显示下拉图标和下拉内容
			contentstatus:true,
			// 默认选中下标
			currentIndex:0,
			// 帮助中心数组
			helpcenterArrary:[]
		}
	},
	onLoad(){
		this._gethelpcenter()
	},
	methods:{
		_gethelpcenter(){
			let obj = {
				
			}
			this.$common.requests('showaboutus',obj).then(res=>{
				// 如果上传成功,就重新请求数据
				if(res.data.code == 200){
					this.helpcenterArrary = res.data.result.records
				}
			})
		},
		toggleclick(){
			this.contentstatus = !this.contentstatus
		},
		changeindex(index){
			this.currentIndex = index
		}
	}
}
